/* ------------------------------------ *\
  CONTENT
\* ------------------------------------ */

.fr-content-media__img img {
    object-fit: cover;
    object-position: 50% 50%;
}

.fr-content-media__img img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.fr-content-media {
    width: 100%;
    margin: 2.5rem 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.fr-content-media__img {
    width: 100%;
    position: relative;
}

.fr-content-media__img::before {
    display: block;
    content: "";
    width: 100%;
    padding-bottom: 56.25%;
}

.fr-content-media__caption, .fr-content-media__transcription {
    width: 100%;
    margin-top: 0.5rem;
    text-align: right;
}

.fr-content-media__caption {
    font-size: 0.75rem;
    line-height: 1.25rem;
}

.fr-content-media__transcription .fr-link {
    font-size: 0.875rem;
    line-height: 1.5rem;
    min-height: 2rem;
    padding: 0.25rem 0.75rem;
    border-radius: 1rem;
    max-width: none;
    max-height: none;
    flex-direction: row-reverse;
    margin-left: -0.75rem;
    margin-right: -0.5rem;
}

.fr-content-media__transcription .fr-link::before {
    font-family: icons !important;
    font-style: normal;
    font-weight: normal !important;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
}

.fr-content-media__transcription .fr-link::before {
    content: "";
}

.fr-content-media__transcription .fr-link::before {
    margin-right: -0.25rem;
    margin-left: 0.5rem;
    font-size: 1rem;
}

.fr-content-media--sm .fr-content-media__img,
.fr-content-media--sm .fr-responsive-vid,
.fr-content-media--sm .fr-content-media__caption,
.fr-content-media--sm .fr-content-media__transcription {
    width: 83.33333%;
}

.fr-content-media--lg .fr-content-media__img,
.fr-content-media--lg .fr-responsive-vid,
.fr-content-media--lg .fr-content-media__caption,
.fr-content-media--lg .fr-content-media__transcription {
    width: 116.66667%;
}

.fr-content-media--lg .fr-content-media__img,
.fr-content-media--lg .fr-responsive-vid {
    max-width: 100vw;
}

.fr-content-media--lg .fr-content-media__caption,
.fr-content-media--lg .fr-content-media__transcription {
    max-width: calc(100vw - 2rem);
}

@media (min-width: 48em) {
    .fr-content-media--sm .fr-content-media__img,
    .fr-content-media--sm .fr-responsive-vid,
    .fr-content-media--sm .fr-content-media__caption,
    .fr-content-media--sm .fr-content-media__transcription {
        width: 75%;
    }

    .fr-content-media--lg .fr-content-media__img,
    .fr-content-media--lg .fr-responsive-vid,
    .fr-content-media--lg .fr-content-media__caption,
    .fr-content-media--lg .fr-content-media__transcription {
        width: 125%;
    }
}
